*{
    padding:0;
    margin:0;
    box-sizing:border-box;

}

.contain{
    width:100vw;
    height:120vh;
    background-image:url("./images/image-header.jpg");
    background-repeat:no-repeat;
    background-size:100% 100%;
    overflow: hidden;
}
.navBar{
    width:100%;
    height:12%;
    color:white;
    display:flex;

    justify-content: flex-end;
    place-items: center;
    padding:0vmin 29vmin 0vmin 0vmin;
}
.head{
    font-size:3vmin;
    font-weight: bold;
    position: absolute;
    left:15%;
}
.navBar >a{
    color:white;
    text-decoration:none;
    font-weight:bold;
    margin-right:2vmin;
    font-size:2vmin;
}
.navBar >a:nth-child(5){
    background:white;
    color:black;
    width:15vmin;
    height:7vmin;
    display:grid;
    place-items:center;
    border-radius:5vmin;

}

.headOne{
    font-size:8vmin;
    font-weight:bold;
    width:100%;
    height:10%;
    text-transform:uppercase;
    padding:3vmin 0vmin 0vmin 0vmin;
    color:white;
    font-family: Fraunces;
    text-align: center;
}

.arrowDown{
    width:5vmin;
    height:15vmin;
    
    background-image: url("./images/icon-arrow-down.svg");
    background-size:100% 100%;
    background-repeat: no-repeat;
    transform: translate(95vmin,10vmin);
}

#menu{
    display:none;
}
.translate{
    transform: translate(30%,10%);
    opacity: 3;
}
.head{
    color:white;
    font-size:5vmin;
    transform:translateY(20%) ;
}

@media only screen and (max-width:550px){
    .contain{
        
    background-image:url("./images/image-header.jpg");
    background-repeat:no-repeat;
    background-size:100% 100%;
    overflow: hidden;
    height:100vh;
    }
    .head{
        color:white;
        font-size:5vmin;
        transform:translateY(7vmin) ;
    }
    .navBar{
        /* display:none */
        width:60%;
        height:35%;
        opacity: 0;
        transition:all 300ms linear;
        z-index:5;
        overflow:hidden;
        display: flex;
        flex-direction: column;
        justify-content: center;
        place-items: center;
        gap:1vmin;
        transform: translate(-130%,10%);
    }
    .navBar >a {
        background:white;
        color:cornflowerblue;
        width:500%;
        height:15%;
        text-align: center;
        display:grid;
        place-items:center;
        font-size:4vmin;
    }
    .navBar > a:hover{
        background:cornflowerblue;
        color:white;
    }
    #menu{
        display:block;
        color:white;

        position: absolute;
        left:10%;
        top:10%;
    }
   

}