*{
    padding:0;
    margin:0;
    box-sizing:border-box;
}

.footer{
    width:100%;
    height:30vh;
    text-align: center;
    background:#90d4c6;
    
}

.footer > h1{
color:#2c7566;
transform: translate(0%,50%);
}
.services{
    transform: translate(0%,70%);
 
}
.services > a{
    font-weight:bold;
    margin:3vmin;
    text-decoration:none;
    font-size:2vmin;
    color:#2c7566;
}

.icons{
    font-size:4vmin;
    transform: translate(0%,10%);

}
.icons > a > i{
    margin:4vmin;
}

.icons >a> i:hover{
    color:white;
}