*{
    padding:0;
    margin:0;
    box-sizing:border-box;
}

.images{
    width:70vw;
    height:45vh;
    overflow: hidden;
    margin-left: 15%;
    display: flex;
    flex-direction:row;
}

.images > img{
    width:25%;
    height: 100%;

}

@media only screen and (max-width:550px){
    .images{
        width:100vw;
        height: 50vh;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        padding:0vmin;
        gap: 1vmin;
        transform: translate(-10vmin);
    }
    .images > img{
        width:45vmin;
        height:45vmin;
    }
}