*{
    padding:0;
    margin:0;
    box-sizing:border-box;
}
.contain1{
    width:100vw;
    height:150vh;
    overflow: hidden;
    position: relative;
}

.firstHalf{
    position:absolute;
    height:75vh;
    width:100%;
   
}
.firstLeft{
    position:absolute;
    left:0%;
    width:50%;
    height:100%;
    
    padding:15vmin 5vmin 0vmin 15vmin;
}
.firstLeft > h1{
    font-size:7vmin;
    margin-bottom: 4vmin;
}
.firstLeft > p{
    font-weight:500;
    width:70%;
    line-height: 3vmin;
    margin-bottom: 4vmin;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.firstLeft > button{
    width:15vmin;
    height:5vmin;
    background:yellow;
    color:black;
    border:none;
    outline:none;
    font-weight:bold;
    font-size:1.5vmin;
}
.firstLeft> button:hover{
    transform: scale(1.2);
    border:1px dotted black;
}

.firstRight{
    position:absolute;
    right:0%;
    width:50%;
    height:100%;
}
.firstRight > img{
    position: absolute;
    width: 100%;height:100%;
  
}

.secondHalf{
    height:75vh;
    position: absolute;
    bottom:0%;
    
   width:100%;
   
}
.secondHalf > img{
    position: absolute;
    width:50%;
    height:100%;
    left:0%;
}

.secondHalf > .secondRight{
    width:50%;
    height: 100%;
    right:0%;
    position:absolute;
    padding:15vmin 5vmin 0vmin 15vmin;

}


.secondRight > h1{
    font-size:7vmin;
    margin-bottom: 4vmin;
}
.secondRight > p{
    font-weight:500;
    width:70%;
    line-height: 3vmin;
    margin-bottom: 4vmin;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.secondRight >  button{
    width:15vmin;
    height:5vmin;
    background:tomato;
    color:rgb(255, 255, 255);
    border:none;
    outline:none;
    font-weight:bold;
    font-size:1.5vmin;
}

.secondRight > button:hover{
    transform: scale(1.2);
    border:1px dotted black;
}




@media only screen and (max-width:550px){
    
   .firstLeft{
       width: 100%;
       height:50%;
       top:0%;
   }
   .firstLeft > h1{
       transform: translateY(-75%);
   }
   .firstLeft > p{
       line-height: 5vmin;
       width:90%;
       transform: translateY(-30%);
   }
   .firstLeft > button{
    transform: translateY(-10vmin);
    width:25vmin;height:7vmin;
    font-size:3vmin;
   }
   .firstLeft > button:hover{
       border:1px dotted black;
       transform: scale(1)translateY(-10vmin);
   }
   .firstRight{
       width: 100%;
       height:50%;
       bottom:0%;;
   }
  
   .secondHalf > img{
       width: 100%;
       height:50%;
       bottom:0%;;
       position: absolute;
   }
   .secondHalf >.secondRight{
    width:100%;
    height:50%;
    position: absolute;
    top:0vmin;
   }
   .secondRight > h1{
     transform: translateY(-10vmin);
   }
   .secondRight > p{
       transform: translateY(-12vmin);
       line-height: 5vmin;
       width:100%;
   }
   .secondRight  > button{
    transform: translateY(-14vmin);
    width:25vmin;height:7vmin;
    font-size:3vmin;
   }
   .secondRight > button:hover{
    border:1px dotted black;
    transform: scale(1)translateY(-14vmin);
}
}