*{
    padding:0;
    margin:0;
    box-sizing:border-box;
}


.reviews{
    width: 100vw;
    height:70vh;
    overflow: hidden;

}
.reviews > h1{
    transform: translate(40%,50%);
}

.cards{
    margin-left:15%;
    position: absolute;
    height: 70%;
    width:70%;
    
    display: flex;
    flex-direction:row;
}

.first,.second,.third{
    width:35%;
    height:100%;
   
    padding:1vmin;
}

.first > img{
    width:10vmin;
    height:10vmin;
    border-radius:50%;
    transform: translate(185%,100%);
}

.first > p {
    transform: translate(0%,550%);
}
.first > h2{
    transform: translate(40%,950%);
}
.first > h3{
    transform: translate(30%,1350%);
}


.second > img{
    width:10vmin;
    height:10vmin;
    border-radius:50%;
    transform: translate(185%,100%);
}

.second > p {
    transform: translate(0%,350%);
}
.second > h2{
    transform: translate(40%,900%);
}
.second > h3{
    transform: translate(30%,1300%);
}



.third > img{
    width:10vmin;
    height:10vmin;
    border-radius:50%;
    transform: translate(185%,100%);
}

.third > p {
    transform: translate(0%,550%);
}
.third > h2{
    transform: translate(40%,955%);
}
.third > h3{
    transform: translate(35%,1363%);
}


@media only screen and (max-width:550px){
    .reviews{
        width:100vw;
        height:100vh;
    }
    .reviews > h1{
        font-size: 5vmin;
        transform: translateX(25vmin)translateY(5vmin);
    }
    .cards{
        width: 95vw;
        height: 80vh;
       
        transform: translate(-13vmin,15vmin);
        display: flex;
        flex-direction:column;
    }
    .first{
        width:100%;
        height:27vh;
        
        position: relative;
    }
    .first > img{
        width:15vmin;
        height:15vmin;
        position: absolute;
        transform: translate(5vmin,8vmin);
    }
    .first > p{
        width: 70%;
        height: 100%;
        transform: translate(25vmin,5vmin);
      
       
    }
    .first > h2{
        width: 70%;
        height: 100%;
        transform: translate(25vmin,-8vmin);
        font-size:6vmin;
       
       
    }
    .first > h3{
        width: 70%;
        height: 100%;
        transform: translate(49vmin,-45vmin);
        font-size:3vmin;
     
       
    }
    .second{
        width:100%;
        height:27vh;
       
        position: relative;
    }
    .second > img{
        width:15vmin;
        height:15vmin;
        position: absolute;
        transform: translate(5vmin,8vmin);
    }
    .second > p{
        width: 70%;
        height: 100%;
        transform: translate(25vmin,5vmin);
      
       
    }
    .second > h2{
        width: 70%;
        height: 100%;
        transform: translate(25vmin,-7vmin);
        font-size:6vmin;
       
       
    }
    .second > h3{
        width: 70%;
        height: 100%;
        transform: translate(55vmin,-47vmin);
        font-size:3vmin;
     
       
    }


    .third{
        width:100%;
        height:27vh;
        
        position: relative;
    }
    .third > img{
        width:15vmin;
        height:15vmin;
        position: absolute;
        transform: translate(5vmin,8vmin);
    }
    .third > p{
        width: 70%;
        height: 100%;
        transform: translate(25vmin,5vmin);
      
       
    }
    .third > h2{
        width: 70%;
        height: 100%;
        transform: translate(25vmin,-8vmin);
        font-size:6vmin;
       
       
    }
    .third > h3{
        width: 70%;
        height: 100%;
        transform: translate(51vmin,-47vmin);
        font-size:3vmin;
     
       
    }
}