*{
    padding:0;
    margin:0;
    box-sizing:border-box;
}
.contain2{
    width:100vw;
    height:100vh;
    overflow: hidden;
    position: relative;
}

.left{
    position:absolute;
    width:50%;
    height: 100%;
    background-image:url("./images/image-graphic-design.jpg");
    background-size:cover;
    background-repeat :no-repeat;
}

.right{
    position:absolute;
    width:50%;
    height: 100%;
    background-image:url("./images/image-photography.jpg");
    background-size:cover;
    background-repeat :no-repeat;
    right:0%;
}

.left > h1{
    position:absolute;
    bottom:10%;
    left: 35%;
}
.left  > p{
    position:absolute;
    bottom:3%;
    width:70%;
    left:10%;
}


.right > h1{
    position:absolute;
    bottom:10%;
    left: 40%;
}
.right  > p{
    position:absolute;
    bottom:3%;
    width:70%;
    left:14%;
}

@media only screen and (max-width:550px){
    .contain2{
        width:100vw;
        height:200vh;
    }
    .left{
        width: 100vw;
        height:100vh;
        top:0%;
        background-image:url("./images/image-graphic-design.jpg");
        background-repeat: no-repeat;
        background-size:100% 100%;
    }
    .left > h1{
        transform: translateY(-35vmin);
    }
    .left  > p{
        width:100%;
        padding:5vmin;
        transform: translate(-5vmin);
    }

    .right{
        width: 100vw;
        height:100vh;
        bottom:0%;
        background-image:url("./images/image-photography.jpg");
        background-repeat: no-repeat;
        background-size:100% 100%;
    }
    .right > h1{
        transform: translateY(-35vmin)translateX(-15vmin);
    }
    .right  > p{
        width:100%;
        padding:5vmin;
        transform: translate(-5vmin);
    }

    
}